<template>
  <div class="reviews">
<!--    <van-nav-bar title="商品评价" :border="false" left-arrow @click-left="goBack"/>-->
      <navigation-bar  @onLeftClick="goBack" pageName="评价"></navigation-bar>
    <div class="content-box">
        <van-pull-refresh v-model="isRefresh" @refresh="refresh()">
              <div class="content" v-show ="evaluateList && evaluateList.length > 0" v-if="!networkError">

                  <div class="titleBox">
                      <div class="left">
                          <div class="title">评分</div>
                      </div>
                      <div class="center">
                          <img :src="selectStartIcon" alt="" v-for="(item, index) in averageNum" :key="index">
                          <img :src="normalStartIcon" alt="" v-for="(item, index) in (5 - averageNum)" :key="index">
                      </div>
                      <div class="title">
                        {{averageAmout}}%好评
                      </div>
                  </div>

                  <van-list
                    v-model="loading"
                    :finished="finished"
                    finished-text="没有更多了"
                    :immediate-check="false"
                    @load="loadMore">
                      <div class="item" v-for="(item, index) in evaluateList" :key = "index">
                          <div class="top">
                              <div class="left">
                                  <div class="head">
                                      <img :src="item.ownerPicUrl ? item.ownerPicUrl : defalutImg" alt="">
                                  </div>
                                  <div class="nickname">{{item.ownerNickname}}</div>
                              </div>
                              <div class="right">
                                  <img :src="selectStartIcon" alt="" v-for="(item, index) in item.evalLevel" :key="index">
                                  <img :src="normalStartIcon" alt="" v-for="(item, index) in (5 - item.evalLevel)" :key="index">
                              </div>
                          </div>
                          <div class="dis">{{item.evalContent}}</div>
                          <div class="img-box" v-if="item.evalPicUrl && item.evalPicUrl.length > 0">
                              <img v-lazy="img.picUrl" alt="" v-for="(img, index1) in item.evalPicUrl" :key="index1" @click="preview(item.evalPicUrl,index1)">
                          </div>
                          <div class="replay" v-if="item.replyContent">
                              <span class="author">商家回复：</span>
                              <span>{{item.replyContent}}</span>
                          </div>
                      </div>
                    </van-list>
              </div>
              <div class="noData" v-show="evaluateList.length == 0 && !loading" v-if="!networkError">
                  <img :src="noDataIcon" alt="">
                  <div>暂无评价</div>
              </div>
              <div style="height:300px;padding-top: 50px;" v-if="networkError">
                <NetworkError></NetworkError>
              </div>

        </van-pull-refresh>
    </div>
    <!-- <Preview :images="images" :loop=false ref="myPreview"></Preview>
     -->

     <van-image-preview v-model="showPreview" :images="images" @change="onChange"  :startPosition="previewIndex" :loop=false :closeable=true close-icon-position="top-left" :close-icon="icon_closeIcon" >
        <!-- <template >左右滑动查看更多</template> -->

      <div slot="cover" class="dot-box" >
        <div class="evalBox">
            <div class="ownerNickName">@{{imageEvalInfo.ownerNickName}}</div>
            <div class="attrValueContent">{{imageEvalInfo.attrValueContent}}</div>
            <div class="evalContent" ref="evalContent">{{imageEvalInfo.evalContent}}</div>
            <img class='evalBgImg' v-if='imageEvalBgImg' src="@/assets/images/mall/imageEvalBg.png" alt="">
        </div>

      </div>
      </van-image-preview>
  </div>
</template>

<script>
import { getAllEvaluateService,getEvalPicUrlService } from "../../services/evaluateService";
import NavigationBar from '@/common/currency/NavigationBar.vue';
import Preview from "@/components/preview/Preview.vue";
import defalutImg from "@/assets/images/find/photo.png";
import selectStartIcon from "@/assets/images/common/icon_evaluate_red.png";
import normalStartIcon from "@/assets/images/common/icon_evaluate_gray.png";

import icon_closeIcon from "@/assets/images/mall/icon_close.png";
import noDataIcon from "@/assets/images/default/no_con_img.png";
import { ImagePreview } from "vant";
import NetworkError from '../../common/networkError/NetworkError';
import { mapState } from "vuex";
export default {
  name: "evaluateList",
  data() {
    return {
      isRefresh: false,
      loading: false,
      finished: false,
      spuCode: "",
      evaluateList: [],
      page: 1,
      row: 10,

      defalutImg: defalutImg,
      selectStartIcon: selectStartIcon,
      normalStartIcon: normalStartIcon,
      noDataIcon: noDataIcon,
      icon_closeIcon:icon_closeIcon,
      // 图片预览属性
      images: [],
      networkError: false,
      averageNum:5,
      averageAmout:100,
      previewIndex:0,
      showPreview:false,
      imageEvalInfos:[],
      imageEvalInfo:{},
      imageEvalBgImg:false,
      imageEvalPages:0,
      evalPicPage:1,
      rows:50,
      evalPicNo:"",
      pages:0


    };
  },
  created() {
    this.spuCode = this.$route.params.spuCode;
    this.getEvaluateList();
  },
  components: {
    "Preview": Preview,
    NetworkError: NetworkError,
      NavigationBar
  },
  computed: mapState({
    evaluateListEventBackButtonFlag: state => state.goBack.evaluateListEventBackButtonFlag
  }),
  watch: {


    imageEvalInfo:function (val){
           this.$nextTick(()=>{
            
               if(this.$refs.evalContent.offsetHeight>60){
                  this.imageEvalBgImg = true;
                  this.$forceUpdate();
               }else{
                  this.imageEvalBgImg = false;
                  this.$forceUpdate();
               }
           })
       },


    /*监控到物理返回键点击状态*/
    evaluateListEventBackButtonFlag(val) {
        if (val) {
            this.$store.dispatch('setEvaluateListEventBackButtonFlag', false);

            if (this.showPreview) {
                this.showPreview = false;
            } else {
                this.$router.go(-1);
            }
        }
    }
  },


  methods: {
    /**
     * 页面返回事件
     */
    goBack() {
      if(this.$store.state.preview.previewId) {
        this.$store.dispatch('setPreviewId', '');
      }
      this.$router.go(-1);
    },

    getEvaluateList() {
      this.loading = true;
      getAllEvaluateService({
        page: this.page,
        row: this.row,
        spuCode: this.spuCode
      }).then(
        result => {
          if (result.resultCode == 0) {
            this.networkError = false;
            if(result.average){
              this.averageAmout =  Math.round(result.average*20 * 100) / 100;
              this.averageNum = Math.round(result.average);
            }

            this.setList(result.data);
          } else {
            this.setError(result.msg);
          }
        },
        error => {
          this.setError(error);
        }
      );
    },

    /**
     * 设置列表数据
     */
    setList(data) {
      this.loading = false;
      this.isRefresh = false;
      this.finished = data.length < 10 ? true : false;
      this.evaluateList =
        this.page == 1 ? data : this.evaluateList.concat(data);
    },

    /**
     * 设置错误信息
     */
    setError(error) {
      this.loading = false;
      this.isRefresh = false;
      if(this.page == 1) {
        this.networkError = true;
      }
      // this.$toast(error);
    },

    /**
     * 下拉刷新
     */
    refresh() {
      this.page = 1;
      this.getEvaluateList();
    },

    /**
     * 上拉加载更多
     */
    loadMore() {
      this.page += 1;
      this.getEvaluateList();
    },

    /**
     * 图片预览
     * @param
     * src    Array    图片url集合
     * index  Number   当前选中图片地址
     **/
    preview(src, index) {
      this.evalPicNo = src[index].evalPicNo;
      this.evalPicPage = 1;
      this.getEvalPicUrl();

    },

    getEvalPicUrl(){
      getEvalPicUrlService({
        page: this.evalPicPage,
        rows: this.rows,
        spuCode: this.spuCode,
        picNo:this.evalPicNo
      }).then(
        result => {
          if (result.resultCode == 0) {
            // this.setList(result.data);
            console.log(this.evalPicPage);

            if(this.evalPicPage == 1)
            {
              this.images = result.data.evalPicDTOS.map(val => val.evalPicUrl);
              this.imageEvalInfos = result.data.evalPicDTOS;
              this.previewIndex = result.data.rowNum-1;
              this.imageEvalInfo = this.imageEvalInfos[this.previewIndex];
              this.showPreview = true;
            }else{
              this.images = this.images.concat(result.data.evalPicDTOS.map(val => val.evalPicUrl));
              this.imageEvalInfos = this.imageEvalInfos.concat(result.data.evalPicDTOS);
            }



            this.pages = result.pages;
            this.$forceUpdate();
            // this.$refs.myPreview.setIsShowPreview(true);
            //  this.$store.dispatch("setPreviewId", this.$refs.myPreview);
          } else {
            // this.setError(result.msg);
          }
        },
        error => {
          this.setError(error);
        }
      );

    },

    onChange(index) {
      this.previewIndex = index;
      this.imageEvalInfo = this.imageEvalInfos[this.previewIndex];

      if(this.pages!=0&&index==this.imageEvalInfos.length-1)
      {
        this.evalPicNo=this.imageEvalInfo.evalPicNo;
        this.evalPicPage++;
        this.getEvalPicUrl();
      }
    }
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="scss">
.reviews {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  .content-box {
    flex: 1;
    overflow: auto;
    /* background:#f9f9f9; */


    .content {
      background:#fff;
      height: 100%;
      /* margin: 10px 0px 10px 0px; */
      width: 100%;
      box-sizing: border-box;
      .titleBox {
          margin:0px 15px 0px 15px;
          display: flex;
          /* justify-content: space-between; */
          align-items: center;
          height: 60px;
          border-bottom: 1px solid #e5e5e5;

          .left {
            display: flex;
            justify-content: flex-start;
            align-items: center;


          }
          .title {

            /* margin-right: 10px; */
            max-width: 200px;
            font-size: 14px;
            color: #333333;
            // font-weight: bold;
            font-family: PingFangSC-Medium;
            overflow: hidden;
            text-overflow: ellipsis;
            white-space: nowrap;
          }
          .center {
            margin-right: 10px;
            align-items: center;
            display: flex;
            box-sizing: border-box;
            img {
              width: 20px;
              height: 20px;
              line-height:60px;
              margin-left: 10px;
            }
          }
        }

      .item {
        margin:0px 15px 0px 15px;
        box-sizing: border-box;
        /* width: 100%; */
        padding: 20px 0;
        border-bottom: 1px solid #e5e5e5;
        .top {
          display: flex;
          /* justify-content: space-between; */
          align-items: center;
          .left {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            .head {
              width: 30px;
              height: 30px;
              img {
                width: 30px;
                height: 30px;
                border-radius: 15px;
              }
            }
            .nickname {
              margin-left: 12px;
              max-width: 200px;
              font-size: 14px;
              color: #333333;
              // font-weight: bold;
              font-family: PingFangSC-Medium;
              overflow: hidden;
              text-overflow: ellipsis;
              white-space: nowrap;
            }
          }
          .right {
            /* margin-left: 10px; */
            display: flex;
            align-items: center;
            img {
              width: 14px;
              height: 14px;
              margin-left: 10px;
            }
          }
        }
        .dis {
          margin-top: 12px;
          width: 100%;
          line-height: 20px;
          font-size: 14px;
          color: #333333;
          font-family: PingFangSC-Regular;
          word-wrap: break-word;
        }
        .img-box {
          display: flex;
          justify-content: flex-start;
          align-items: center;
          margin-top: 15px;
          img {
            width: 78px;
            height: 78px;
            margin-right: 11px;
            border-radius: 4px;
            object-fit: contain;
          }
          img:nth-child(4) {
            margin: 0;
          }
        }
        .replay {
          margin-top: 11px;
          padding: 10px 12px;
          box-sizing: border-box;
          font-family: PingFangSC-Regular;
          word-wrap: break-word;
          font-size: 14px;
          color: #3a3a3a;
          line-height: 20px;
          .author {
            color: #5e9be3;
          }
        }
      }
    }
    .noData {
      padding-top: 168px;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      img {
        width: 158px;
        height: 112px;
      }
      div {
        font-family: PingFangSC-Regular;
        font-size: 16px;
        color: #9a9a9a;
        letter-spacing: 0.06px;
        text-align: center;
        margin-top: 50px;
      }
    }
  }
}
/deep/.van-pull-refresh {
  min-height: 100%;
}
/deep/.van-pull-refresh__track {
  min-height: 100%;
}
/deep/.network {
  background: #ffffff;
}
/deep/.van-image-preview__cover {
    top: auto;
    bottom: 0px;
    /* background-image: linear-gradient(180deg, rgba(0,0,0,0.00) 21%, #000000 100%); */
    background-image: linear-gradient(180deg, rgba(0,0,0,0.00) 0%, #000000 100%);
    width: 100%;
    height: 130px;
    padding-bottom: 0;
    padding-bottom: constant(safe-area-inset-bottom);
    padding-bottom: env(safe-area-inset-bottom);
    .dot-box{

      .evalBox{
        // width: 100%;
        top: auto;
        bottom: 0px;
        max-height:100PX;
        position: fixed;
        overflow: scroll;
        font-size: 14px;
        margin :10px 15px 10px 15px;
        box-sizing: border-box;
        .ownerNickName{
          width: 100%;
          font-family: PingFangSC-Regular;
          font-size: 14px;
          line-height: 20PX;
          color: #FFFFFF;
          letter-spacing: -0.04px;

        }
        .attrValueContent{
          width: 100%;
          font-family: PingFangSC-Regular;
          font-size: 14px;
          line-height: 20PX;
          color: #9A9A9A;
          letter-spacing: -0.04px;
        }
        .evalContent{
          width: 100%;
          font-family: PingFangSC-Regular;
          font-size: 14px;
          line-height: 20PX;
          color: #FFFFFF;
          letter-spacing: -0.04px;
        }
        .evalBgImg{
          position: fixed;
          top: auto;
          bottom: 10px;
          height: 30PX;
          width: 100%;
        }
      }

    }


  }
  /deep/.van-image__loading {
    display: none!important;
  }
  /deep/.van-image-preview__close-icon--top-left
  {
    top:30px;
  }
  /deep/.van-icon__image{
  width: 16px;
  height: 16px;

  }

  /deep/.van-image-preview__index{
    top: 35px;
  }


</style>
